body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  color: #363636;
}

a {
  color: inherit;
}

button {
  cursor: pointer;
}

header {
  padding: 15px;
}

.header-logo {
  width: 100%;
  max-width: 160px;
}

.cart-button {
  background: #363636;
  color: #fff;
  display: inline-block;
  margin-bottom: 15px;
}

.cart-button:hover {
  color: #ccc;
  text-decoration: none;
}

.categories {
  list-style-type: square;
  padding-left: 20px;
}

.product-card,
.product-card div {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
}

.product-card img {
  max-height: 140px;
  max-width: 100%;
  display: block;
}

.product-card p {
  margin-bottom: 0;
}

footer {
  padding: 15px;
}

.product-extended-image {
  width: 100%;
}

.cartItem button {
  margin: 0 6px;
}
